<template>
	<div class='cell-wrap' :class="{'bordertop':borderTop,'active':active}">
		<div class="cell-left">
			<slot name="img"></slot><span>{{title}}</span>
		</div>
		<div class='cell-right'>
			<span><img src="../assets/images/ic_seti_arrow_right_gray.png" alt="" /></span>
		</div>
	</div>
</template>

<script>
	export default {
		props:{
			title:{
				type:String,
				default:'',
			},
			borderTop:{
				type:Boolean,
				default:false,
			},
			active:{
				type:Boolean,
				default:false,
			}
		}
	}
</script>

<style lang='less'>
	.cell-wrap{
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
		border-bottom: 1px solid #ccc;
		padding: 4px 0 5px 0;
		.cell-left {
			span {
				position: absolute;
				top:7px;
				left:26px;
				color:#333333
			}
			img {
				height: 26px;
			}
		}
		.cell-right {
			img {
				height: 18px;
			}
		}
	}
	.bordertop {
		margin-top:1px;
		border-top: 1px solid #ccc;
	}
	.active {
		border-left:3px solid #007AFF;
	}
</style>


