<template>
	<div class="tabbar">
		<slot></slot>
	</div>
</template>

<script type="text/javascript">
	export default {
		props:['value'],
		computed:{
			value(){
				return this.$route.matched[0].name;
			}
		}
	}
</script>

<style lang='less'>
	.tabbar {
		display: flex;
		width: 100%;
		height: 50px;
		border:1px solid #ccc;
		position: fixed;
		bottom: 0;
		z-index: 999;
		background-color: #eee;
	}
</style>


