<template>
	<div class="cell-media-wrap">
		<router-link to="/cid">
			<div class="cell-media-detail">
			<div class="detail-left">
				<div><slot name="title"></slot></div>
				<div><slot name="desc"></slot></div>
			</div>
			<div class="detail-right"> 
				<div class="img-wrap">
					<slot name="imgwrap"></slot>
				</div>
			</div>
		</div>
		<div class="author">
			<span>作者:{{authorName}}</span>
		</div>
		</router-link>
	</div>
</template>

<script>
	export default {
		props:{
			authorName:{
				type:String,
				default:'admin'
			}
		},
	}
</script>

<style lang='less'>
	.cell-media-wrap {
		display: flex;
		flex-direction: column;
		padding: 5px 3px;
		a {
			color: #333333;
			text-decoration: none;
		}
		.cell-media-detail {
			display: flex;
			justify-content: space-between;
			.detail-right {
				img {
					width:100%
				}
				.img-wrap{
					width:100px;
					height:100px;
				}
			}
			.detail-left {
				position: relative;
				flex-grow: 1;
				h3 {
				  color:#333333;
				  display:-webkit-box;
				  -webkit-line-clamp:2;
				  -webkit-box-orient:vertical;
				  overflow:hidden;
				}
				span{
				  position: absolute;
				  bottom: 10px;
             	  color:#333333;
				  display:-webkit-box;
				  -webkit-line-clamp:1;
				  -webkit-box-orient:vertical;
				  overflow:hidden;
				}
			}
		}
		.author {
			border-bottom: 1px solid #CCCCCC;
			padding: 5px 0;
			span {
				font-size: 13px;
			}
		}
	}
	
</style>


