<template>
  <div id="app">
  	<router-view></router-view>
    <tabbar v-model="tabName">
      <tab-item tid="index" isRouter> 
      	<img src="./assets/images/ic_tab_home_normal.png" alt="" slot="normal-icon">
      	<img src="./assets/images/ic_tab_home_active.png" alt="" slot="active-icon">
      	首页
      </tab-item>
        <tab-item tid="reade" :isRouter="true"> 
      	<img src="./assets/images/ic_tab_subject_normal.png" alt="" slot="normal-icon">
      	<img src="./assets/images/ic_tab_subject_active.png" alt="" slot="active-icon">
      	阅读
      </tab-item>
      </tab-item>
        <tab-item tid="member" isRouter> 
      	<img src="./assets/images/ic_tab_profile_normal.png" alt="" slot="normal-icon">
      	<img src="./assets/images/ic_tab_profile_active.png" alt="" slot="active-icon">
      	个人中心
      </tab-item>
    </tabbar>
  </div>
</template>

<script>
import tabbar from './components/botton-bar'
import tabItem from './components/botton-bar-item'

export default {
  name: 'app',
  components:{
    tabbar,
    tabItem
  },
  data(){
  	return {
  		tabName:'index'
  	}
  },
}
</script>

<style>
*{
  padding:0;margin:0;
}
body {
	overflow-x:hidden;
	font-family: "Helvetica Neue", Helvetica, STHeiTi, Arial, sans-serif;
}
</style>



