<template>
	<a href="javascript:;" class="tab-item" @click="goRouter()">
		<span v-show="!isActive"><slot name="normal-icon"></slot></span>
		<span v-show="isActive"><slot name="active-icon"></slot></span>
		<span><slot></slot></span>
	</a>
</template>

<script>
export default {
	props:{
		tid:{
			type:String,
		},
		isRouter:{
			type:Boolean,
			default:false,
		}
	},
	computed:{
		isActive(){
			if(this.tid==this.$parent.value) {
				return true;
			}
		}
	},
	methods:{
		goRouter(){
			this.$parent.$emit('input',this.tid);
			if(this.isRouter) {
				this.$router.push(this.tid)
			}
		}
	}
}
</script>

<style lang='less'>
	.tab-item {
		display: flex;
		flex-direction: column;
		flex-grow:1;
		text-align:center;
		text-decoration: none;
		color: #666;
		font-size: 13px;
		img {
			height: 30px;
		}
	}
	
</style>


