<template>
	<div id='carousel' ref="wrap">
		<transition-group name='slide' tag='ul'> 
			<li v-for="(item,index) in images" v-show="index==mark" :key="index"><a href=""><img :class="{'firstImg':index==0}" :src="item.src" /></a></li>
		</transition-group>
	</div>
</template>

<script>
		export default {
			data(){
				return {
					mark:0,
					flag:true,
					images:[
						{src:'http://upload-images.jianshu.io/upload_images/3360875-5625658440cb542d.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240'},
						{src:'http://upload-images.jianshu.io/upload_images/3360875-b70e9d81d26e2a27.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240'},
						{src:'http://upload-images.jianshu.io/upload_images/3360875-dc724649454c2ddc.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240'},
					]
				}
			},
			mounted(){
				var wrap = this.$refs.wrap;
				var h = document.querySelector('.firstImg').height/3;
				wrap.style.height = h + 'px';
				this.autoplay();
			},
			methods:{
				autoplay(){
					var that = this;
					setInterval(function(){
						that.mark = that.mark + 1;
						if(that.mark>2) {
							that.mark = 0;
						}
					},3000)
				},
				changes(){
					
				}
			}
		}
</script> 

<style lang='less'>
	#carousel {
		position: relative;
		overflow: hidden;
		width:100%;
		margin-top: 50px;
			li {
				width: 100%;
				position: absolute;
				padding: 0;
				margin: 0;
				font-size: 0;
				display: flex;
				justify-content: center;
			}
			&:after {
				content: '.';
				height: 0;
				clear:both;
			}
	}
	img {
		display: inline-block;
		max-width:100%;
	}
	.slide-enter-active {
		transform: translateX(0);
		transition: all 1s ease;
	}
	.slide-leave-active {
		transform: translateX(-100%);
		transition: all 1s ease;
	}
	.slide-enter {
		transform: translateX(100%);
	}
	.slide-leave {
		transform: translateX(0);
	}
</style>


