<template>
	<div>
		<swiper></swiper>
		<cell title="提醒" :borderTop="true">
			<img slot="img" src="../../assets/images/ic_mine_notification.png"/>
		</cell>
		<cell title="设置"></cell>
		<cell title="热门" :active="true"></cell>
		<cell-media v-for='(item,index) in datas' :authorName="item.target.author.name" :key='index'>
			<h3 slot="title">{{item.title}}</h3>
			<img :src="item.target.author.avatar" alt="" slot='imgwrap' />
		</cell-media>
	</div>
</template>

<script>
	import swiper from '../../components/swiper'
	import cell from '../../components/cell'
	import cellMedia from '../../components/cell-media'
	import homedata from '../../data/homeData.js'
	import mHeader from '../../components/header'
	export default {
		components:{
			swiper,
			cellMedia,
			cell,
			mHeader
		},
		data(){
			return {
				datas:[],
			}
		},
		mounted(){
			this.getData();
			let _this = this;
			setTimeout(function(){
				window.scrollTo(0,_this.$store.state.bodyTop)
			},100);
		},
		methods:{ 
			getData(){
			var that = this;
//				this.axios.get('/api/homedata').then(function(res){
//					for(var i = 0;i<res.data.data.recommend_feeds.length;i++) {
//						if(res.data.data.recommend_feeds[i].card ) {
//						if(typeof res.data.data.recommend_feeds[i].card == 'object'){
//							if(res.data.data.recommend_feeds[i].card.name=="为你推荐") {
//								that.datas.push(res.data.data.recommend_feeds[i]);
//							}
//						}
//					}
//					}
////					console.log(that.datas)
//				})
				let feeds = homedata.recommend_feeds;
				for(var i=0;i<feeds.length;i++) {
					if(feeds[i].card) {
						if(feeds[i].card.name == '为你推荐') {
							this.datas.push(feeds[i])
						}
					}
				}
			}
		}
		
	}
</script>


