import Vue from 'vue'
import Router from 'vue-router'
import index from '../pages/index/index'
import reade from '../pages/reade/reade'
import member from '../pages/member/member'
import show from '../components/show'
import animate from '../pages/index/animate'

Router.prototype.goBack = function(){
	this.isBack = true;
	window.history.go(-1)
}
Vue.use(Router)

export default new Router({
  routes: [
    {
   		path:'/',
   		name:'index',
   		component:index,
   		children:[
   			{
   				path:'',
   				component:animate
   			},
   			{
    			path:'/cid',
    			component:show,
   	 		}
   		],
    },
    {
    	path:'/reade',
    	name:'reade',
    	component:reade
    },
    {
    	path:'/index',
    	redirect:'/'
    },
    {
    	path:'/member',
    	name:'member',
    	component:member,
    },
    {
    	path:'*',
    	redirect:'/'
    },
  
  ]
})



