<template>
	<div>
	<reade class="nav-bar" :isBg="false" :isShow="false">会员中心</reade>
	<div class="top-bar">
		<div class="bar-left">
			<img src="../../assets/images/ic_avatar_default.png" alt="" />
		</div>
		<div class="bar-right">
			<span>登陆/注册</span>
		</div>
	</div>
	<cell title="提醒" :borderTop="true">
		<img slot="img" src="../../assets/images/ic_mine_notification.png"/>
	</cell>
	</div>
</template>

<script>
	import reade from '../../components/header'
	import cell from '../../components/cell'
	export default {
		components:{
			reade,
			cell
		}
	}
</script>
<style lang='less'>
	.nav-bar {
		position: static !important;
	}
	.top-bar {
		width:100%;
		height:100px;
		display: flex;
		.bar-left {
			flex-grow:1;
			position: relative;
			img{
				position: absolute;
				top:20px;
				left:10%;
			};
		}
		.bar-right {
			flex-grow:2;
			position: relative;
			span{
				border:1px solid #CCCCCC;
				position: absolute;
				top:30%;
				right:5%;
				padding:10px 40px;
			};
		}
	}
</style>


