<template>
	<header class="nav" :class="{'nav-bgcolor':isBg}">
		<div class="nav-left"></div>
		<h3><slot></slot></h3>
		<div class="nav-right">
			<img v-show="isShow" src="../assets/images/ic_group_search_small.png" alt="" />
		</div>
	</header>
</template>

<script>
	export default {
		name:'nav',
		props:{
			isShow:{
				type:Boolean,
				default:false
			},
			isBg:{
				type:Boolean,
				default:true
			}
		}
	}
</script>

<style lang='less'>
	.nav {
		display: flex;
		position: fixed;
		z-index: 666;
		width:100%;
		height: 50px;
		border:1px solid #ccc;
		flex-direction: row;
		.nav-left {
			width:70px;
		}
		&.nav-bgcolor {
			background-color: #42BD56;
		}
		h3 {	
			flex-grow: 1;
			color:#333;
			text-align: center;
			line-height: 50px;
			font-weight: normal;
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow:hidden
		}
		.nav-right {
			width:70px;
			position: relative;
			img {
				width:30px;
				position: absolute;
				right:6px;
				top:10px;
			}
		}
	}
</style>


