</template>
<script type="text/javascript">
	export default {
		mounted(){
			var bodytop = document.body.scrollTop;
			this.$store.commit('change',bodytop)
			window.scrollTo(0,0)
		},
		methods:{
			goback(){
				this.$router.goBack()
			}
		},
	}
</script>

<style lang='less'>
	.wrap-text {
		margin-top:50px;
		background-color: #fff;
	}
</style>


